/*
导入了阿里巴巴字体库，参考：https://www.iconfont.cn/help/detail?spm=a313x.7781069.1998910419.16&helptype=code
官方列举了三种导入方式：
    1. unicode引用（html+css）
        支持ie6+、不太直观
    2. font-class引用（css）
        支持ie8+、class定义图标、使用字体
    3. symbol引用（js）
        支持 ie9+、支持多色图标、svg渲染
*/
.iconfont{
  width: 1em;
  height: 1em;
  line-height: 1em;
}
.iconsymbol {
  width: 1em;
  height: 1em;
  line-height: 1em;
  vertical-align: middle;
  fill: currentColor;
  overflow: hidden;
}
